* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #fff;
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
}

#navbar {
  color: #000;
  z-index: 1000;
  background: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 3% 5%;
  transition: all .3s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

#navbar ul {
  gap: 30px;
  list-style: none;
  display: flex;
}

#navbar ul li {
  border: none;
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
  font-size: 15px;
  list-style: none;
}

#navbar ul li a {
  color: #000;
  font-size: 15px;
  text-decoration: none;
}

#navbar ul li a:hover {
  color: #fb4f00;
  border: none;
  text-decoration: underline;
}

#navbar ul a {
  color: #000;
  border: none;
  text-decoration: none;
}

#navbar button {
  color: #fff;
  cursor: pointer;
  background-color: #fb4f00;
  border: none;
  border-radius: 20px;
  padding: 5px 10px;
}

.logo h3 {
  color: #000;
  font-family: Libre Baskerville, serif;
  font-size: 18px;
  font-weight: bold;
}

.logo span {
  color: red;
}

.hamburger {
  cursor: pointer;
  flex-direction: column;
  gap: 5px;
  display: none;
}

.hamburger div {
  background-color: #000;
  border-radius: 2px;
  width: 25px;
  height: 3px;
}

main {
  background-color: #fff;
}

main p {
  text-align: center;
  justify-content: center;
  margin-top: 10%;
  margin-bottom: 10%;
  font-size: 80px;
  display: flex;
}

#imgcontainer, #imgcontainer2 {
  text-align: center;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-bottom: 5%;
  display: flex;
}

#imgcontainer img, #imgcontainer2 img {
  object-fit: cover;
  width: 10px;
  height: 100vh;
}

button {
  background-color: #fb4f00;
  border: none;
  border-radius: 20px;
  width: 150px;
  height: 45px;
  margin: 20px auto;
  display: flex;
}

a:hover {
  color: #fb4f00;
  background-color: #fff;
  border: 2px solid #fb4f00;
  width: 160px;
  transform: scale(1.1);
}

button a {
  color: #fff;
  border-radius: 20px;
  padding-top: 10%;
  padding-left: 18%;
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
  text-decoration: none;
  transition: color .3s, background-color .3s, transform .3s;
}

#qoute p {
  font-size: 50px;
}

#qoute p:last-of-type {
  margin-top: -9%;
  font-family: sans-serif;
  font-size: 20px;
}

footer {
  color: #fff;
  background-color: #000;
  padding: 5%;
}

footer section {
  justify-content: space-between;
  gap: 30px;
  display: flex;
}

.nav {
  flex-direction: column;
  gap: 10px;
  width: 35%;
  display: flex;
}

.nav h3 {
  font-family: sans-serif;
  font-size: 17px;
  font-weight: bold;
}

.nav ul li {
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
  font-size: 15px;
  list-style: none;
}

.nav ul li a {
  color: #fff;
  text-decoration: none;
}

.nav ul li a:hover {
  color: #fb4f00;
  background-color: #0000;
  border: none;
  text-decoration: none;
  transform: scale(1.1);
}

#reserve {
  justify-content: center;
  align-items: center;
  font-size: 12px;
  display: flex;
}

@media (width <= 1024px) {
  main p {
    font-size: 60px;
  }

  #imgcontainer img, #imgcontainer2 img {
    height: 80vh;
  }

  footer section {
    justify-content: space-between;
    gap: 30px;
    height: 70px;
    display: flex;
  }

  .nav {
    flex-direction: column;
    gap: 10px;
    width: 35%;
    display: flex;
  }

  .nav h3 {
    font-family: sans-serif;
    font-size: 15px;
    font-weight: bold;
  }

  .nav ul li {
    font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
    font-size: 12px;
    list-style: none;
  }

  .nav ul li a {
    color: #fff;
    text-decoration: none;
  }

  .nav ul li a:hover {
    color: #fb4f00;
    background-color: #0000;
    border: none;
    transform: scale(1.1);
  }

  #reserve {
    justify-content: center;
    align-items: center;
    font-size: 10px;
    display: flex;
  }
}

@media (width <= 768px) {
  #navbar ul {
    background: #fff;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    width: 100%;
    padding: 20px 0;
    display: none;
    position: absolute;
    top: 60px;
    right: 0;
    box-shadow: 0 4px 10px #0000001a;
  }

  #qoute p {
    margin-top: 5%;
    font-size: 30px;
  }

  #qoute p:last-of-type {
    margin-top: -9%;
    font-family: sans-serif;
    font-size: 15px;
  }

  #navbar ul.show, .hamburger {
    display: flex;
  }

  main p {
    margin-top: 30%;
    font-size: 40px;
  }

  #imgcontainer img, #imgcontainer2 img {
    width: 100%;
    height: 70vh;
  }

  .nav {
    flex-direction: column;
    gap: 5px;
    width: 35%;
    display: flex;
  }

  .nav h3 {
    font-family: sans-serif;
    font-size: 15px;
    font-weight: bold;
  }

  .nav ul li {
    font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
    font-size: 12px;
    list-style: none;
  }

  .nav ul li a {
    color: #fff;
    text-decoration: none;
  }

  .nav ul li a:hover {
    color: #fb4f00;
    background-color: #0000;
    border: none;
    transform: scale(1.1);
  }

  #reserve {
    justify-content: center;
    align-items: center;
    margin-top: 5%;
    font-size: 10px;
    display: flex;
  }
}

@media (width <= 480px) {
  main p {
    font-size: 30px;
  }

  #imgcontainer img, #imgcontainer2 img {
    height: 60vh;
  }
}
